.promo {
  margin-top: -113px;
  overflow: hidden;
  color: $white;
  background-color: #1b2d37;
  background-image: url("../img/bg-image-desktop@1x.png");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  min-height: 878px;
}

.promo__wrapper {
  margin-top: 150px;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.promo__description {
  padding-top: 163px;
}

.promo__title {
  margin: 0;
  padding-left: 19px;
  padding-bottom: 28px;
  font-family: $roboto-condensed;
  font-style: normal;
  font-weight: bold;
  @include font(80, 94);
  letter-spacing: 0.07em;
  text-transform: uppercase;
}

.promo__text {
  width: 608px;
  margin: 0;
  padding-bottom: 43px;
  padding-left: 117px;
  @include font(22, 34);
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 16px;
    left: 22px;
    background-image: url("../img/line-decoration.png");
    background-size: 66px 2px;
    background-repeat: no-repeat;
    background-position: 0 0;
    width: 66px;
    height: 2px;
  }
}

.promo__button {
  padding-left: 118px;

  span:first-child {
    display: inline-block;
    padding-top: 18px;
    padding-bottom: 18px;
    padding-left: 45px;
    padding-right: 45px;
    min-width: 315px;

    font-family: $roboto-condensed;
    font-weight: 700;

    @include font(16, 19);
    letter-spacing: 0.05em;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    //border: 1px solid #ffffff;
    background-color: #00b2ff;
    border-radius: 5px;

    cursor: pointer;

    &:hover,
    &:focus {
      background-color: #0089eb;
    }

    &:active {
      background-color: #2100f5;
    }
  }

  span:last-child {
    display: none;
  }
}

.promo__image {
  position: relative;
  width: 705px;
  height: 515px;
  padding-top: 107px;
  right: -55px;
}

.promo__anchor {
  position: absolute;
  bottom: 22px;
  left: 9px;
  transform: rotate(-90deg);
  @include font(14, 16);
  color: #ffffff;
  opacity: 0.5;
}
