@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed"), local("RobotoCondensed-Regular"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed Bold"), local("RobotoCondensed-Bold"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: local("PT Mono"), local("PTMono-Regular"), url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

body {
  min-width: 320px;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  font-style: normal;
  font-weight: 400;
  color: #666666; }

a {
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  --webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.container {
  max-width: 1460px;
  margin: 0 auto;
  padding-left: 25px;
  padding-right: 25px; }

.page-header {
  padding-top: 33px;
  padding-bottom: 29px;
  color: #ffffff;
  background-color: #1b2d37; }

.page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  /*justify-content: space-between;*/
  align-items: center; }

.page-header__logo {
  width: 315px;
  display: flex;
  padding-left: 24px;
  padding-right: 5px; }

.page-header__icon-logo {
  padding-right: 13px; }

.page-header__logo-text {
  margin: 0;
  padding-top: 5px;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }
  .page-header__logo-text span {
    width: 144px;
    display: block;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 29px;
    color: #ffffff; }

.page-header__nav {
  width: 40px;
  padding-right: 390px; }

.page-header__button-menu {
  font-size: 0;
  border: none;
  background-color: #182d37; }

.page-header__contacts {
  margin-left: auto; }

.page-header__email {
  padding-right: 77px; }
  .page-header__email span {
    line-height: 18px;
    color: #ffffff; }

.page-header__icon-email {
  padding-right: 10px; }

.page-header__phone {
  padding-right: 50px; }
  .page-header__phone span {
    line-height: 18px;
    color: #ffffff; }

.page-header__icon-phone {
  padding-right: 7px; }

.page-header__button {
  display: inline-block;
  margin-right: 25px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.9px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  border: 1px solid #ffffff;
  background-color: #182d37;
  border-radius: 5px;
  cursor: pointer; }

.promo {
  margin-top: -113px;
  overflow: hidden;
  color: #ffffff;
  background-color: #1b2d37;
  background-image: url("../img/bg-image-desktop@1x.png");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  min-height: 878px; }

.promo__wrapper {
  margin-top: 150px;
  display: flex;
  justify-content: space-between;
  position: relative; }

.promo__description {
  padding-top: 163px; }

.promo__title {
  margin: 0;
  padding-left: 19px;
  padding-bottom: 28px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.promo__text {
  width: 608px;
  margin: 0;
  padding-bottom: 43px;
  padding-left: 117px;
  font-size: 22px;
  line-height: 34px;
  position: relative; }
  .promo__text::before {
    content: "";
    position: absolute;
    top: 16px;
    left: 22px;
    background-image: url("../img/line-decoration.png");
    background-size: 66px 2px;
    background-repeat: no-repeat;
    background-position: 0 0;
    width: 66px;
    height: 2px; }

.promo__button {
  padding-left: 118px; }
  .promo__button span:first-child {
    display: inline-block;
    padding-top: 18px;
    padding-bottom: 18px;
    padding-left: 45px;
    padding-right: 45px;
    min-width: 315px;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.05em;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    background-color: #00b2ff;
    border-radius: 5px;
    cursor: pointer; }
    .promo__button span:first-child:hover, .promo__button span:first-child:focus {
      background-color: #0089eb; }
    .promo__button span:first-child:active {
      background-color: #2100f5; }
  .promo__button span:last-child {
    display: none; }

.promo__image {
  position: relative;
  width: 705px;
  height: 515px;
  padding-top: 107px;
  right: -55px; }

.promo__anchor {
  position: absolute;
  bottom: 22px;
  left: 9px;
  transform: rotate(-90deg);
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.5; }
